import React, { useEffect, useState } from "react";
import ColumnDivider from "../common/culumnDivider/ColumnDivider";
import CustomButton from "../common/customButton/CustomButton";
import TextInput from "../common/textInput/TextInput";
import product_1 from "../../assets/Images/product_1.jpg";
import noImage from "../../assets/Images/no-image.png";
import DataPagination from "../common/dataPagination/DataPagination";
import { apiUrl } from "../../config/api";

const ProductList = (props) => {
  const { selectProduct, addToCart } = props;

  const [products, setProducts] = useState([]);
  const [search, setSearch] = useState("");
  const [trigger, setTrigger] = useState(0);

  // pagination functions
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [dataPerPage] = useState(8);
  // Get current data
  const indexOfLastData = currentPage * dataPerPage;
  const indexOfFirstData = indexOfLastData - dataPerPage;
  const currentData = data.slice(indexOfFirstData, indexOfLastData);

  // Change page
  const paginate = (e, pageNumber) => {
    setCurrentPage(pageNumber);
    e.preventDefault();
  };

  //search product by name from API
  useEffect(() => {
    const url = `${apiUrl}search/findProductByName/${search}`;
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
        console.log(data);
      });
  }, [search]);

  useEffect(() => {
    const url = `${apiUrl}product/allProducts`;
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
        console.log(data);
      });
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(search);
    e.target.reset();
  };
  const handleChange = (e) => {
    const newData = { ...search };
    newData[e.target.name] = e.target.value;
    setSearch(newData.searchProduct);
  };
  return (
    <div>
      <form onSubmit={handleSubmit} className="custom_form_style">
        <ColumnDivider design="pos_product_list_search">
          <TextInput
            onChange={(e) => handleChange(e)}
            name="searchProduct"
            label="Search Product"
          />
          <CustomButton
            design="pos_product_list_search_btn"
            type="submit"
            label="Search"
            icon=""
          />
        </ColumnDivider>
      </form>
      <div className="pos_product_list pb-5">
        {currentData.map((item, index) => (
          <div className="product_card">
            {item.productImage ? (
              <img src={item.productImage} alt="" />
            ) : (
              <img src={noImage} alt="" />
            )}

            <p>
              {item.productName} <br /> {item.productCode}
            </p>
            <p>Price: {item.buyPrice}</p>
            <p>Stock: {item.openingStock}</p>
            <button
              onClick={() => {
                addToCart(item);
              }}
            >
              add
            </button>
          </div>
        ))}
      </div>

      <DataPagination
        dataPerPage={dataPerPage}
        totalData={data.length}
        paginate={paginate}
        currentPage={currentPage}
        indexOfLastData={indexOfLastData}
        indexOfFirstData={indexOfFirstData}
      />
    </div>
  );
};

export default ProductList;
