import React from 'react';
import CustomButton from '../common/customButton/CustomButton';
import PageTitle from '../common/pageTitle/PageTitle';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import { useNavigate, useParams } from 'react-router';
import AddProduct from './AddProduct';
import ManageProduct from './ManageProduct';
import './Products.scss'


const breadcrumbData = [
    {
        label: "Product",
        link: '/dashboard/products'
    }
]

const Products = () => {
    const navigate = useNavigate()
    const { ppId,pagesId} = useParams()

    const navigateHandler_1 = () => {
        navigate('/dashboard/products/addProduct')
    }
    const navigateHandler_2 = () => {
        navigate('/dashboard/products/manageProduct')
    }



    return (
        <div className="products_section">
            <PageTitle title="Products" breadcrumbData={breadcrumbData} />
            <div className="product_section_box">
                <CustomButton label="Add Product" onClick={() => navigateHandler_1()} icon={faHome} />
                <CustomButton label="Manage Product" onClick={() => navigateHandler_2()} icon={faHome} />
            </div>

            <div className="products_section_body">
            {
                    ((pagesId === 'products') && (ppId === undefined)) ? <AddProduct />  : ''
                }


                {
                    (ppId === "addProduct") ? <AddProduct /> :
                        (ppId === "manageProduct") ? <ManageProduct /> : ""
                }






            </div>
        </div>
    );
};

export default Products;