import { faHome } from '@fortawesome/free-solid-svg-icons';
import React from 'react';
import { useNavigate } from 'react-router';
import CustomButton from '../common/customButton/CustomButton';
import PageTitle from '../common/pageTitle/PageTitle';
import TodayPosSales from './TodayPosSales';



const breadcrumbData = [
    {
        label: "Sales",
        link: '/dashboard/sales'
    }
]



const Sales = () => {

    const navigate = useNavigate()

    const navigateHandler_1 = () => {
        navigate('/dashboard/sales')
    }
    const navigateHandler_2 = () => {
        navigate('/dashboard/posCreate')
    }
    return (
        <div className="products_section">
            <PageTitle title="Sales" breadcrumbData={breadcrumbData} />

            <div className="product_section_box">
                <CustomButton label="Sales" onClick={() => navigateHandler_1()} icon={faHome} />
                <CustomButton label="New Sell" onClick={() => navigateHandler_2()} icon={faHome} />
            </div>

            <TodayPosSales />
        </div>
    );
};

export default Sales;