import React from 'react';
import './SelectInput.scss'

const SelectInput = ({ label, options = [], ...res }) => {
    const data = [...options]
    return (
        <div className="file_input_box">
            <label >{label}</label>
            <select className="form-select  mb-3" aria-label=".form-select-lg example" {...res}>
                <option >Select {label}</option>

                {
                    data.map((item, index) =>
                        <>
                            {
                                item.title ? <option key={index} value={item._id}>{ item.title}</option> :

                                item.name ? <option key={index} value={item._id}>{item.name}</option> :
                                item.productName ? <option key={index} value={item._id}>{item.productName + " - " + item.productCode}</option> :''


                            }

                        </>




                    )
                }


            </select>
        </div>
    );
};

export default SelectInput;