import React, { useEffect, useState } from "react";
import ColumnDivider from "../common/culumnDivider/ColumnDivider";
import CustomButton from "../common/customButton/CustomButton";
import DataPagination from "../common/dataPagination/DataPagination";
import DataTable from "../common/dataTable/DataTable";
import PageTitle from "../common/pageTitle/PageTitle";
import SectionTitle from "../common/sectionTitle/SectionTitle";
import TextInput from "../common/textInput/TextInput";
import "./DailyReports.scss";
import { apiUrl } from "../../config/api";

const breadcrumbData = [
  {
    label: "Today Reports",
    link: "/dashboard/dailyReports",
  },
];

const DailyReports = () => {
  const formatYmd = (date) => date.toISOString().slice(0, 10);
  const [stock, setStock] = useState([]);
  const [search, setSearch] = useState(formatYmd(new Date()));
  const [trigger, setTrigger] = useState(0);

  // pagination functions
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [dataPerPage] = useState(10);
  // Get current data
  const indexOfLastData = currentPage * dataPerPage;
  const indexOfFirstData = indexOfLastData - dataPerPage;
  const currentData = data.slice(indexOfFirstData, indexOfLastData);

  // Change page
  const paginate = (e, pageNumber) => {
    setCurrentPage(pageNumber);
    e.preventDefault();
  };

  //products data load from API
  useEffect(() => {
    const url = `${apiUrl}report/allSaleDailyReport/${search}`;
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
        console.log(data, "ok data");
      });
  }, [trigger]);

  const handleSubmit = (e) => {
    setTrigger((x) => x + 1);
    e.preventDefault();
    e.target.reset();
  };
  const handleChange = (e) => {
    const newData = { ...search };
    newData[e.target.name] = e.target.value;
    setSearch(newData.searchDate);
  };

  return (
    <div className="products_section">
      <PageTitle title="Product Stock" breadcrumbData={breadcrumbData} />

      <div className="products_section_body">
        <SectionTitle label="Product Stock" />
        <form onSubmit={handleSubmit} className="custom_form_style py-3">
          <ColumnDivider design="stock_list_search">
            <TextInput
              type="date"
              onChange={(e) => handleChange(e)}
              name="searchDate"
              label="Search by Date"
            />
            <CustomButton
              design="stock_list_search_btn"
              type="submit"
              label="Search"
              icon=""
            />
          </ColumnDivider>
        </form>

        <DataTable>
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Invoice No.</th>
              <th scope="col">Customer</th>
              <th scope="col">Items</th>
              <th scope="col">Date</th>
              <th scope="col">Discount</th>
              {/* <th scope="col">Receivable</th> */}
              <th scope="col">Items Price</th>
              <th scope="col">Shipping Price</th>
              <th scope="col">Tax Price</th>
              {/* <th scope="col">Product Returned</th> */}
              {/* <th scope="col">Due</th> */}
              <th scope="col">Total Cost</th>
              <th scope="col">Profit</th>
              <th scope="col">Status</th>
            </tr>
          </thead>
          <tbody>
            {currentData.map((item, index) => (
              <tr>
                <td>{index + 1}</td>
                <td>{item._id}</td>
                <td>{item.customer?.name}</td>
                <td>{item.sellingProducts.length}</td>
                <td>{item.purchaseDate}</td>
                <td>N/A</td>
                <td>{item.itemsPrice}</td>
                <td>{item.shippingPrice}</td>
                <td>{parseInt(item.taxPrice).toFixed(2)}</td>
                <td>{item.totalPrice}</td>

                <td>@mdo</td>
                <td>Paid</td>
              </tr>
            ))}
          </tbody>
        </DataTable>
        <DataPagination
          dataPerPage={dataPerPage}
          totalData={data.length}
          paginate={paginate}
          currentPage={currentPage}
          indexOfLastData={indexOfLastData}
          indexOfFirstData={indexOfFirstData}
        />
      </div>
    </div>
  );
};

export default DailyReports;
