import React from "react";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faCalendarWeek, faCog, faExpandArrowsAlt } from '@fortawesome/free-solid-svg-icons'
import { faBell, faUser } from "@fortawesome/free-regular-svg-icons";
import { Link, NavLink } from "react-router-dom";


const Header = ({ desToggleHandler }) => {

    return (
        <>
            <div className="app_mobile_logo">
                <NavLink to="/" className="app_logo">
                    <div className="logos">
                        Pos
                        <span>Tech</span>
                    </div>
                </NavLink>

            </div>
            <header className="main_header clearfix">
                <div className="logo_box">
                    <button onClick={() => desToggleHandler()}>
                        <FontAwesomeIcon icon={faBars} />
                    </button>

                    <Link to="/">
                        <div className="logos">
                            Pos
                            <span>Tech</span>
                        </div>
                    </Link>


                </div>

                <nav className="navbar_main navbar-static-top pl-10">
                    <div className="left_header">
                        <FontAwesomeIcon icon={faCalendarWeek} />
                    </div>
                    <div className="right_header d-flex">
                        <FontAwesomeIcon icon={faExpandArrowsAlt} />
                        <FontAwesomeIcon icon={faBell} />
                        <FontAwesomeIcon icon={faUser} />
                        <FontAwesomeIcon icon={faCog} />
                    </div>
                </nav>

            </header>
        </>
    );
};

export default Header;