import { faTrash } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useEffect, useState } from "react";
import ColumnDivider from "../common/culumnDivider/ColumnDivider";
import CustomButton from "../common/customButton/CustomButton";
import DataTable from "../common/dataTable/DataTable";
import SectionTitle from "../common/sectionTitle/SectionTitle";
import SelectInput from "../common/selectInput/SelectInput";
import TableInput from "../common/tableInput/TableInput";
import TextInput from "../common/textInput/TextInput";
import rightMarkImage from "../../assets/Images/right.png";
import { ToastContainer, toast } from "react-toastify";
import { apiUrl } from "../../config/api";

const PlaceNewSell = (props) => {
  const { cartProduct, addToCart, removeToCart, deleteCartProduct } = props;
  const [newPos, setNewPos] = useState([]);
  const [customer, setCustomer] = useState([]);
  const notifySuccess = () => toast.success("New Brand Added");
  const notifyError = () => toast.error("Brad Added Failed ok me");
  const [stockUpdate, setStockUpdate] = useState([]);

  // total selling Count
  const itemsPrice = cartProduct.reduce(
    (a, c) => a + c.buyQuantity * c.buyPrice,
    0
  );
  const taxPrice = itemsPrice * 0.14;
  const shippingPrice = itemsPrice > 2000 ? 0 : 20;
  const totalPrice = itemsPrice + taxPrice + shippingPrice;

  // data load from API
  useEffect(() => {
    const url = apiUrl + "people/allCustomer";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setCustomer(data);
      });
  }, []);

  const add = () => {
    cartProduct.map((item) => {
      const url2 = `${apiUrl}pos/stockUpdate`;
      fetch(url2, {
        method: "POST",
        body: JSON.stringify(item),
        headers: {
          "Content-type": "application/json",
        },
      })
        .then((res) => res.json())
        .then((data) => {})
        .catch((err) => {});
    });
  };

  const handleSubmit = async (e) => {
    try {
      const url = apiUrl + "pos/createNewSell";
      fetch(url, {
        method: "POST",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify(newPos),
      })
        .then((res) => res.json())
        .then((data) => {
          console.log(data, "ok");
          notifySuccess();
          add();
        });
    } catch (error) {
      console.log("Data was not inserted Successfully");
      notifyError();
    }

    e.preventDefault();
    console.log(newPos, "test");
  };

  const handleAddData = (e) => {
    const newData = { ...newPos };
    newData.sellingProducts = cartProduct;
    newData.itemsPrice = itemsPrice;
    newData.taxPrice = taxPrice;
    newData.shippingPrice = shippingPrice;
    newData.totalPrice = totalPrice;
    setNewPos(newData);
    e.preventDefault();
  };

  const handleChange = (e) => {
    const newData = { ...newPos };
    newData[e.target.name] = e.target.value;
    setNewPos(newData);
    e.preventDefault();
  };

  return (
    <>
      <ToastContainer />
      <form onSubmit={handleSubmit}>
        <ColumnDivider>
          <SelectInput
            onChange={(e) => handleChange(e)}
            name="customer"
            label="Customer Id"
            options={customer}
          />
          <CustomButton
            design="add_customer_btn"
            label="Add Customer"
            icon=""
          />
        </ColumnDivider>
        {/* <TextInput onChange={(e) => handleChange(e)} name='productCode'  value={selectProduct.selectProduct.productCode}  label="Customer Name" /> */}
        <TextInput
          onChange={(e) => handleChange(e)}
          name="purchaseDate"
          type="date"
          label="Date"
        />
      </form>

      <br />
      <SectionTitle label="Selected Items" />

      <DataTable>
        <thead>
          <tr className="pos_table_row">
            <th>#</th>
            <th scope="col">Name</th>
            <th scope="col">Quantity</th>
            <th scope="col">Stock</th>
            <th scope="col">Price</th>
            <th scope="col">Sub Total</th>
            <th scope="col">
              <FontAwesomeIcon icon={faTrash} />
            </th>
          </tr>
        </thead>
        <tbody>
          {cartProduct.map((item, index) => (
            <tr className="pos_table_row" onChange={(e) => handleChange(e)}>
              <td>{index + 1}</td>
              <td>{item.productName}</td>

              <td>
                <button onClick={(e) => addToCart(item)} className="btn">
                  +
                </button>
                <span>{item.buyQuantity} </span>
                <button onClick={(e) => removeToCart(item)} className="btn">
                  -
                </button>
              </td>
              <td>{item.openingStock}</td>

              <td>{item.buyPrice}</td>
              <TableInput
                name="productQuantity"
                label=""
                Value={item.buyQuantity * item.buyPrice}
                disabled
              />
              <td>
                <button
                  onClick={(e) => deleteCartProduct(item)}
                  className="btn"
                >
                  <FontAwesomeIcon icon={faTrash} />
                </button>
              </td>
            </tr>
          ))}

          <tr className="pos_table_row_total">
            {cartProduct.length !== 0 && (
              <>
                <td>
                  Items Price: <strong>${itemsPrice.toFixed(2)}</strong>
                </td>
                <td>
                  Tax Price: <strong>${taxPrice.toFixed(2)}</strong>
                </td>
                <td>
                  Shipping Price: <strong>${shippingPrice.toFixed(2)}</strong>
                </td>
                <td colSpan="3">
                  Total: <strong>${totalPrice.toFixed(2)}</strong>
                </td>
              </>
            )}
          </tr>
        </tbody>
      </DataTable>

      <CustomButton
        type="button"
        data-bs-toggle="modal"
        data-bs-target="#staticBackdrop"
        label="Payment"
        icon=""
        onClick={handleAddData}
      />

      {/* modals payment confirm*/}
      <div
        className="modal fade"
        id="staticBackdrop"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabindex="-1"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-body text-center mb-5 modal_body_desing">
              <img src={rightMarkImage} className="img_responsive" alt="" />
              <h1>Are You Sure?</h1>
              <p>
                Do you really want to submit these records? This process cannot
                be undone.
              </p>
              <div class="btn-group">
                <button
                  type="button"
                  className="btn btn-danger btn-lg mr-2 rounded-lg"
                  data-bs-dismiss="modal"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  className="btn btn-success btn-lg rounded-lg"
                  onClick={handleSubmit}
                >
                  Confirm
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default PlaceNewSell;
