import React, { useEffect, useState } from "react";
import ColumnDivider from "../common/culumnDivider/ColumnDivider";
import CustomButton from "../common/customButton/CustomButton";
import DataPagination from "../common/dataPagination/DataPagination";
import DataTable from "../common/dataTable/DataTable";
import PageTitle from "../common/pageTitle/PageTitle";
import SectionTitle from "../common/sectionTitle/SectionTitle";
import TextInput from "../common/textInput/TextInput";
import "./Stock.scss";
import { apiUrl } from "../../config/api";

const breadcrumbData = [
  {
    label: "Product Stock",
    link: "/dashboard/stock",
  },
];

const Stock = () => {
  const [stock, setStock] = useState([]);
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [dataPerPage] = useState(10);
  // Get current data
  const indexOfLastData = currentPage * dataPerPage;
  const indexOfFirstData = indexOfLastData - dataPerPage;
  const currentData = data.slice(indexOfFirstData, indexOfLastData);

  // Change page
  const paginate = (e, pageNumber) => {
    setCurrentPage(pageNumber);
    e.preventDefault();
  };

  //products data load from API
  useEffect(() => {
    const url = apiUrl + "product/allProducts";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
        console.log(data, "ok data");
      });
  }, []);

  const [search, setSearch] = useState({});
  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(search);
    e.target.reset();
  };
  const handleChange = (e) => {
    const newData = { ...search };
    newData[e.target.name] = e.target.value;
    setSearch(newData);
  };

  return (
    <div className="products_section">
      <PageTitle title="Product Stock" breadcrumbData={breadcrumbData} />

      <div className="products_section_body">
        <SectionTitle label="Product Stock" />
        <form onSubmit={handleSubmit} className="custom_form_style py-3">
          <ColumnDivider design="stock_list_search">
            <TextInput
              onChange={(e) => handleChange(e)}
              name="searchProduct"
              label="Search Product"
            />
            <TextInput
              onChange={(e) => handleChange(e)}
              name="searchProduct"
              label="Search by Code"
            />
            <CustomButton
              design="stock_list_search_btn"
              type="submit"
              label="Search"
              icon=""
            />
          </ColumnDivider>
        </form>

        <DataTable>
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Product Code</th>
              <th scope="col">Product Name</th>
              {/* <th scope="col">Category</th> */}
              <th scope="col">Buy Price</th>
              <th scope="col">Sell Price</th>
              <th scope="col">Purchase</th>
              <th scope="col">Available Stock</th>
              <th scope="col">Sell Value</th>
            </tr>
          </thead>
          <tbody>
            {currentData.map((item, index) => (
              <tr>
                <td>{index + 1}</td>
                <td>{item.productCode}</td>
                <td>{item.productName}</td>
                <td>{item.buyPrice}</td>
                <td>{item.sellPrice}</td>
                <td>{"..."}</td>
                <td>{item.openingStock}</td>
                <td>{item.openingStock * item.sellPrice}</td>
              </tr>
            ))}
          </tbody>
        </DataTable>
        <DataPagination
          dataPerPage={dataPerPage}
          totalData={data.length}
          paginate={paginate}
          currentPage={currentPage}
          indexOfLastData={indexOfLastData}
          indexOfFirstData={indexOfFirstData}
        />
      </div>
    </div>
  );
};

export default Stock;
