import React, { Component } from 'react';
import ApexCharts from 'apexcharts'
import ReactApexChart from 'react-apexcharts'
import './Barchart_1.scss'
import SectionTitle from '../../sectionTitle/SectionTitle';

class Barchart_1 extends Component {


    constructor(props) {
        super(props);
        const date=(date=0) => new Date(Date.now()-date).toLocaleDateString()

    
        this.state = {
        
          series: [{
            name: 'Sales',
            data: [31, 40, 28, 51, 42, 109, 100]
          }, {
            name: 'Purchase',
            data: [11, 32, 45, 90, 34, 52, 41]
          }],
          options: {
            chart: {
              height: 300,
              type: 'area'
            },
            dataLabels: {
              enabled: false
            },
            stroke: {
              curve: 'smooth'
            },
            xaxis: {
              type: 'date',
              // categories: [date(86400000),date(86400000), "2018-09-19T01:30:00.000Z", "2018-09-19T02:30:00.000Z", "2018-09-19T03:30:00.000Z", "2018-09-19T04:30:00.000Z", "2018-09-19T05:30:00.000Z", "2018-09-19T06:30:00.000Z"]
              categories: [date(),date(86400000), date(86400000*2), date(86400000*3), date(86400000*3), date(86400000*4), date(86400000*5), date(86400000*6)]
            },
            tooltip: {
              x: {
                // format: 'dd/MM/yy HH:mm'
                format: 'dd/MM/yy'
                // format: new Date().toLocaleDateString()
              },
            },
          },
        
        
        };
      }

    render() {
        return (
            <div className="barchart_1">
              <div class="des_box_header">
							<h4 class="des_box_title">Our Sales vs. In  Purchase Cost</h4>
						</div>
							<hr />

            <ReactApexChart options={this.state.options} series={this.state.series} type="area" height={300} />
          </div>
        );
    }
}

export default Barchart_1;