import React, { useState } from "react";
import CustomButton from "../common/customButton/CustomButton";
import SectionTitle from "../common/sectionTitle/SectionTitle";
import TextInput from "../common/textInput/TextInput";
import { ToastContainer, toast } from "react-toastify";
import { apiUrl } from "../../config/api";

const AddCategory = () => {
  const [newCategory, setNewCategory] = useState({});
  const notifySuccess = () => toast.success("New Brand Added");
  const notifyError = () => toast.error("Brad Added Failed");
  const handleSubmit = (e) => {
    fetch(apiUrl + "product/createNewCategory", {
      method: "POST",
      headers: {
        "Content-type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify(newCategory),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data, "ok");
        notifySuccess();
      })
      .catch((err) => {
        console.log("Data was not inserted Successfully");
        notifyError();
      });
    e.target.reset();
    e.preventDefault();
  };
  const handleChange = (e) => {
    const newData = { ...newCategory };
    newData[e.target.name] = e.target.value;
    setNewCategory(newData);
  };
  return (
    <>
      <ToastContainer />
      <form onSubmit={handleSubmit} className="custom_form_style">
        <SectionTitle label="Add Category" />
        <TextInput
          onChange={(e) => handleChange(e)}
          name="title"
          label="Category Name"
        />
        <CustomButton type="submit" label="Submit" />
      </form>
    </>
  );
};
export default AddCategory;
