import React, { Component } from 'react';
import ReactApexChart from 'react-apexcharts'
import './ColumnChart_1.scss'

class ColumnChart_1 extends Component {
    constructor(props) {
        super(props);
        const date = (date = 0) => new Date(Date.now() - date).toLocaleDateString()
        const colors = ["#5948d5", '#F43D53']

        this.state = {

            series: [{
                name: 'Items',
                data: [44, 55, 57, 56, 61, 58, 63, 60, 66]
            }, {
                name: 'Purchase Cost',
                data: [76, 85, 101, 98, 87, 105, 91, 114, 94]
            }],

            options: {
                chart: {
                    type: 'bar',
                    height: 350
                },
                colors: colors,
                plotOptions: {


                    bar: {
                        horizontal: false,
                        endingShape: 'rounded',
                        columnWidth: '30%',
                        // colors: colors

                    },
                },
                dataLabels: {
                    enabled: false
                },
                stroke: {
                    show: true,
                    width: 2,
                    colors: ['transparent'],
                    // colors:colors
                },
                xaxis: {
                    // categories: ['Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct'],
                    categories: [date(), date(86400000), date(86400000 * 2), date(86400000 * 3), date(86400000 * 3), date(86400000 * 4), date(86400000 * 5), date(86400000 * 6)]

                },
                yaxis: {
                    title: {
                        // text: '$ (thousands)'
                    }
                },
                fill: {
                    opacity: 1,
                    // shade: 'dark',

                    colors: colors



                },
                tooltip: {
                    y: {
                        formatter: function (val) {
                            return "$ " + val + " thousands"
                        }
                    }
                }

            },
        };
    }
    render() {
        return (

            <div id="chart_columnChar_1" className="column_chart_1">
                <div class="des_box_header">
                    <h4 class="des_box_title">Items vs. In  Purchase Cost</h4>
                </div>
                <hr />
                <ReactApexChart options={this.state.options} series={this.state.series} type="bar" height={350} />
            </div>
        );
    }
}

export default ColumnChart_1;