import React, { useState } from 'react';
import PageTitle from '../common/pageTitle/PageTitle';
import SectionTitle from '../common/sectionTitle/SectionTitle';
import PlaceNewSell from './PlaceNewSell';
import './Pos.scss'
import ProductList from './ProductList';

const breadcrumbData = [
    {
        label: "Pos Manage",
        link: '/dashboard/posCreate'
    }
]



const Pos = () => {

    const [cartProduct, setCartProduct] = useState([])
    console.log(cartProduct);

    const addToCart = (product) => {
        const exist = cartProduct.find(x => x._id === product._id)
        if (exist) {
            setCartProduct(cartProduct.map(x =>
                x._id === product._id ? { ...exist, buyQuantity: exist.buyQuantity + 1, openingStock: exist.openingStock - 1 } : x
            ))
        } else {
            setCartProduct([...cartProduct, { ...product, buyQuantity: 1 }])
        }
    }


    const removeToCart = (product) => {
        const exist = cartProduct.find(x => x._id === product._id)
        if (exist.buyQuantity === 1) {
            setCartProduct(cartProduct.filter((x) => x._id !== product._id));
        } else {
            setCartProduct(
                cartProduct.map((x) =>
                    x._id === product._id ? { ...exist, buyQuantity: exist.buyQuantity - 1, openingStock: exist.openingStock + 1 } : x
                )
            );
        }
    }


    const deleteCartProduct = (product) => {

        const exist = cartProduct.find(x => x._id === product._id)
        if (exist) {
            setCartProduct(cartProduct.filter((x) => x._id !== product._id));
        }
    }




    return (
        <div>
            <PageTitle title="Pos Manage" breadcrumbData={breadcrumbData} />

            <div className="pos_box">

                <div className="left">
                    <SectionTitle label="Place New Sell" />
                    <PlaceNewSell cartProduct={cartProduct} deleteCartProduct={deleteCartProduct} addToCart={addToCart} removeToCart={removeToCart} />


                </div>
                <div className="right">
                    <SectionTitle label="Product List" />
                    <ProductList addToCart={addToCart} selectProduct={cartProduct} />
                </div>



            </div>


        </div>
    );
};

export default Pos;