import { faHome } from '@fortawesome/free-solid-svg-icons';
import React from 'react';
import { useNavigate, useParams } from 'react-router';
import CustomButton from '../common/customButton/CustomButton';
import PageTitle from '../common/pageTitle/PageTitle';
import AddSuppliers from './AddSuppliers';
import './Suppliers.scss'
import ManageSuppliers from './ManageSuppliers';


const breadcrumbData = [
    {
        label: "Suppliers",
        link: '/dashboard/suppliers'
    }
]
const Suppliers = () => {

    const navigate = useNavigate()
    const { ppId,pagesId } = useParams()

    const navigateHandler_1 = () => {
        navigate('/dashboard/suppliers/addSupplier')
    } 
    const navigateHandler_2 = () => {
        navigate('/dashboard/suppliers/manageSuppliers')
    }


    return (
        <div className="products_section">
            <PageTitle title="Suppliers" breadcrumbData={breadcrumbData} />
            <div className="product_section_box">
                <CustomButton label="Add Category" onClick={() => navigateHandler_1()} icon={faHome} />
                <CustomButton label="Manage Categories" onClick={() => navigateHandler_2()} icon={faHome} />
            </div>


            <div className="products_section_body">
                {
                    ((pagesId === 'suppliers') && (ppId === undefined)) ? <AddSuppliers /> : ''
                }

                {
                    (ppId === "addSupplier") ? <AddSuppliers /> :
                        (ppId === "manageSuppliers") ? <ManageSuppliers /> : ""
                }
            </div>



        </div>
    );
};

export default Suppliers;