import React from 'react';
import { useParams } from 'react-router-dom';
import Brands from '../brands/Brands';
import Categories from '../categories/Categories';
import Customers from '../customers/Customers';
import Pos from '../pos/Pos';
import Products from '../products/Products';
import Purchase from '../purchase/Purchase';
import Sales from '../sales/Sales';
import Stock from '../stock/Stock';
import Suppliers from '../suppliers/Suppliers';
import DailyReports from '../dailyReports/DailyReports';
import MonthlyReports from '../monthlyReports/MonthlyReports';
import DashboardContent from '../dashboard/DashboardContent';

const DashboardBody = () => {
    let { pagesId } = useParams()

    return (
        <>
            {
                (pagesId === 'pos') ? <DashboardContent /> :
                    (pagesId === 'products') ? <Products /> :
                        (pagesId === 'categories') ? <Categories /> :
                            (pagesId === 'brands') ? <Brands /> :
                                (pagesId === 'posCreate') ? <Pos /> :
                                    (pagesId === 'sales') ? <Sales /> :
                                        (pagesId === 'purchase') ? <Purchase /> :
                                            (pagesId === 'stock') ? <Stock /> :
                                                (pagesId === 'customers') ? <Customers /> :
                                                    (pagesId === 'suppliers') ? <Suppliers /> :
                                                        (pagesId === 'dailyReports') ? <DailyReports /> :
                                                            (pagesId === 'monthlyReports') ? <MonthlyReports /> : ''
            }

        </>
    );
};

export default DashboardBody;