import React from 'react';
import { faBook } from '@fortawesome/free-solid-svg-icons'
import { faUser } from "@fortawesome/free-regular-svg-icons";
import SideNavLink from '../common/sideNavLink/SideNavLink';

const sideNavData = [

    {
        icon: faUser,
        text: "Dashboard",
        link: "/dashboard/pos"
    },
    {
        sectionInfo: 'Sale & Purchase',
        icon: faUser,
        text: "Pos",
        link: "/dashboard/posCreate"
    },
    {
        icon: faUser,
        text: "Sales",
        link: "/dashboard/sales"
    },
    {
        icon: faUser,
        text: "Purchase",
        link: "/dashboard/purchase"
    },
    {
        icon: faUser,
        text: "Stock",
        link: "/dashboard/stock"
    },
    {
        sectionInfo: 'product Info',
        icon: faBook,
        text: "Products",
        link: "/dashboard/products"
    },

    {
        icon: faBook,
        text: "Categories",
        link: "/dashboard/categories"
    },

    {
        icon: faBook,
        text: "Brands",
        link: "/dashboard/brands"
    },
    {
        sectionInfo: 'Peoples',
        icon: faUser,
        text: "Customers",
        link: "/dashboard/customers"
    },
    {
        icon: faUser,
        text: "Suppliers",
        link: "/dashboard/suppliers"
    },
    {
        sectionInfo: 'Reports',
        icon: faUser,
        text: "Daily Reports",
        link: "/dashboard/dailyReports"
    },
    {
        icon: faUser,
        text: "Monthly Report",
        link: "/dashboard/monthlyReports"
    },
    {
        sectionInfo: 'Settings & Customize',
        icon: faUser,
        text: "Settings",
        link: "/dashboard/Settings"
    },
    {
        icon: faUser,
        text: "Logout",
        link: "/dashboard/Logout"
    }

]





const Sidebar = ({ desToggle }) => {
    return (
        <>
            <aside className="sidebar_main">
                <ul className={desToggle ? "sidebar_nav" : "sidebar_nav toggle_sidebar_nav"}>
                    {
                        sideNavData.map((item, index) => <SideNavLink key={index} item={item} />)
                    }
                </ul>
            </aside>
        </>
    );
};

export default Sidebar;