import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { NavLink } from 'react-router-dom';

const SideNavLink = ({ item }) => {
    const { icon, text, link, sectionInfo } = item
    return (
        <>
            <p className="navbar_section_info">{sectionInfo}</p>
            <li>
                <NavLink to={link} className={(navInfo) => navInfo.isActive ? "navActiveLink" : ''}>
                    <span className="nav_icon"><FontAwesomeIcon icon={icon} /></span>
                    <span className="nav_text">{text}</span>
                </NavLink>

            </li>
        </>
    );
};

export default SideNavLink;