import React from 'react';
import CustomButton from '../common/customButton/CustomButton';
import PageTitle from '../common/pageTitle/PageTitle';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import { useNavigate, useParams } from 'react-router';
import AddBrand from './AddBrands';
import ManageBrands from './ManageBrands';



const breadcrumbData = [
    {
        label: "Brands",
        link: '/dashboard/brands'
    }
]

const Brands = () => {
    const navigate = useNavigate()
    const { ppId,pagesId} = useParams()

    const navigateHandler_1 = () => {
        navigate('/dashboard/brands/addBrand')
    }
    const navigateHandler_2 = () => {
        navigate('/dashboard/brands/manageBrands')
    }

    return (
        <div className="products_section">
            <PageTitle title="Brands" breadcrumbData={breadcrumbData} />
            <div className="product_section_box">
                <CustomButton label="Add Brand" onClick={() => navigateHandler_1()} icon={faHome} />
                <CustomButton label="Manage Brands" onClick={() => navigateHandler_2()} icon={faHome} />
            </div>

            <div className="products_section_body">
            {
                    ((pagesId === 'brands') && (ppId === undefined)) ? <AddBrand /> : ''
                }

                {
                    (ppId === "addBrand") ? <AddBrand /> :
                        (ppId === "manageBrands") ? <ManageBrands /> : ""
                }
            </div>
        </div>
    );
};

export default Brands;