import React, { useEffect, useState } from "react";
import DataPagination from "../common/dataPagination/DataPagination";
import DataTable from "../common/dataTable/DataTable";
import EditDeleteButton from "../common/editAndDeleteButton/EditDeleteButton";
import EditModal from "../common/editModal/EditModal";
import SectionTitle from "../common/sectionTitle/SectionTitle";
import TextInput from "../common/textInput/TextInput";
import noImage from "../../assets/Images/no-image.png";
import { toast, ToastContainer } from "react-toastify";
import { faEdit, faTrashAlt } from "@fortawesome/free-regular-svg-icons";
import ColumnDivider from "../common/culumnDivider/ColumnDivider";
import SelectInput from "../common/selectInput/SelectInput";
import FileInput from "../common/fileInput/FileInput";
import TextArea from "../common/textArea/TextArea";
import axios from "axios";
import BrandAndCategory from "./BrandAndCategory";
import { apiUrl } from "../../config/api";

const ManageProduct = () => {
  const notifySuccess = (text) => toast.success(text);
  const notifyError = (text) => toast.error(text);
  const [modalData, setModalData] = useState({});
  const [brands, setBrands] = useState([]);
  const [ok, setOk] = useState(true);
  const [categories, setCategories] = useState([]);
  const [products, setProducts] = useState([]);

  // pagination functions
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [dataPerPage] = useState(10);
  // Get current data
  const indexOfLastData = currentPage * dataPerPage;
  const indexOfFirstData = indexOfLastData - dataPerPage;
  const currentData = data.slice(indexOfFirstData, indexOfLastData);

  // Change page
  const paginate = (e, pageNumber) => {
    setCurrentPage(pageNumber);
    e.preventDefault();
  };

  //products data load from API
  useEffect(() => {
    const url = apiUrl + "product/allProducts";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
        console.log(data, "ok data");
      });
  }, []);

  useEffect(() => {
    const url = apiUrl + "product/allBrands";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setBrands(data);
      });
  }, []);

  // all category loader
  useEffect(() => {
    const url = apiUrl + "product/allCategories";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setCategories(data);
      });
  }, []);

  const deleteHandler = (id) => {
    console.log(id);
    const url = `${apiUrl}product/deleteProduct/${id}`;
    fetch(url, {
      method: "DELETE",
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data, "dataaa");
        notifySuccess("Brand Delete Successfully");
      })
      .catch((err) => {
        notifyError("Brand Not Delete Successfully");
      });

    if (ok === true) {
      setOk(false);
    } else {
      setOk(true);
    }
  };

  const handleUpdate = (e) => {
    const url = `${apiUrl}product/updateProduct`;
    fetch(url, {
      method: "POST",
      body: JSON.stringify(modalData),
      headers: {
        "Content-type": "application/json",
      },
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data, "dataaa");
        notifySuccess("Product Data Updated Successfully");
      })
      .catch((err) => {
        notifyError("Product Data Updated Failed");
      });

    e.preventDefault();
  };

  const editHandler = (data) => {
    setModalData(data);
  };
  const handleChange = (e) => {
    const newData = { ...modalData, ...imageURL };
    newData[e.target.name] = e.target.value;
    setModalData(newData);
  };
  const [imageURL, setImageURL] = useState({});

  const handleChangeFile = (e) => {
    const imageData = new FormData();
    imageData.set("key", "59b9f92bf1b5d3036dd00cceba773135");
    imageData.append("image", e.target.files[0]);

    axios
      .post("https://api.imgbb.com/1/upload", imageData)
      .then(function (response) {
        setImageURL({
          productImage: response.data.data.display_url,
        });
      })
      .catch(function (error) {});
  };

  return (
    <>
      <ToastContainer />
      <div>
        <SectionTitle label="Manage Product " />
        <DataTable>
          <thead>
            <tr>
              <th scope="col">#</th>
              <th scope="col">Picture</th>
              <th scope="col">Code</th>
              <th scope="col">Name</th>
              <th scope="col">Category</th>
              <th scope="col">Brand</th>
              <th scope="col">Buy Price</th>
              <th scope="col">Sell Price</th>
              <th scope="col">Current Stock</th>
              <th scope="col">Action</th>
            </tr>
          </thead>
          <tbody>
            {currentData.map((item, index) => (
              <tr key={index}>
                <td>{index + 1}</td>
                <td className="manageProductImg">
                  {item.productImage ? (
                    <img src={item.productImage} alt="" />
                  ) : (
                    <img src={noImage} alt="" />
                  )}
                </td>
                <td>{item.productCode}</td>
                <td>{item.productName}</td>
                <td>{item.category?.title}</td>
                <td>{item.brand?.title}</td>
                <td>{item.buyPrice}</td>
                <td>{item.sellPrice}</td>
                <td>{"N/A"}</td>

                <td>
                  <EditDeleteButton
                    onClick={(e) => editHandler(item)}
                    data-bs-toggle="modal"
                    data-bs-target="#editModal"
                    icon={faEdit}
                  />
                  <EditDeleteButton
                    onClick={() => deleteHandler(item._id)}
                    design="redDelete"
                    icon={faTrashAlt}
                  />

                  <EditModal
                    modalTitle="Update Brand Name"
                    handleSubmit={handleUpdate}
                  >
                    <TextInput
                      onChange={(e) => handleChange(e)}
                      name="productName"
                      label="Product Name"
                      value={modalData.title}
                    />
                    <TextInput
                      onChange={(e) => handleChange(e)}
                      name="productCode"
                      label="Product Code"
                      value={modalData.productCode}
                    />
                    <ColumnDivider>
                      {/* <SelectInput onChange={(e) => handleChange(e)} name='brand' options={brands} value={modalData.brand.title} label="Brand" /> */}
                      {/* <SelectInput onChange={(e) => handleChange(e)} name='category' options={categories} value={modalData.category.title} label="Category" /> */}
                    </ColumnDivider>

                    <ColumnDivider>
                      <TextInput
                        onChange={(e) => handleChange(e)}
                        name="buyPrice"
                        label="Buy Price"
                        value={modalData.buyPrice}
                      />
                      <TextInput
                        onChange={(e) => handleChange(e)}
                        name="sellPrice"
                        label="Sell Price"
                        value={modalData.sellPrice}
                      />
                    </ColumnDivider>

                    <TextInput
                      onChange={(e) => handleChange(e)}
                      name="openingStock"
                      label="Opening Stock"
                      value={modalData.openingStock}
                    />

                    <FileInput
                      onChange={(e) => handleChangeFile(e)}
                      label="Product Image"
                      name="productImage"
                    />
                    <TextArea
                      onChange={(e) => handleChange(e)}
                      name="description"
                      label="Product Description"
                      value={modalData.description}
                    />
                  </EditModal>
                </td>
              </tr>
            ))}
          </tbody>
        </DataTable>

        <DataPagination
          dataPerPage={dataPerPage}
          totalData={data.length}
          paginate={paginate}
          currentPage={currentPage}
          indexOfLastData={indexOfLastData}
          indexOfFirstData={indexOfFirstData}
        />
      </div>
    </>
  );
};

export default ManageProduct;
