import React, { useState } from "react";
import "./Dashboard.scss";
import Header from "../header/Header";
import Sidebar from "../sidebar/Sidebar";
import DashboardBody from "../dashboardBody/DashboardBody";
import DashboardContent from "./DashboardContent";

const Dashboard = ({ content }) => {

  // toggle handler func
  const [desToggle, setDesToggle] = useState(true)
  const desToggleHandler = () => {
    if (desToggle === true) {
      setDesToggle(false)
    }
    else {
      setDesToggle(true)
    }
  }

  return (
    <>
      <div className={desToggle ? "wrapper" : "wrapper desToggle_wrapper "} id="wrapper">
        {/* main header start */}
        <Header desToggleHandler={desToggleHandler} />
        {/* main header end */}

        <main className="main_container">

          {/* sidebar start */}
          <Sidebar desToggle={desToggle} />
          {/* sidebar end */}

          {/* app body  start */}
          <div className="main_body">
            {
              content && <DashboardContent />
            }
            <DashboardBody />
          </div>
          {/* app body  end */}

        </main>
      </div>
    </>
  );
};

export default Dashboard;
