import { faEdit, faTrashAlt } from "@fortawesome/free-regular-svg-icons";
import React, { useEffect, useState } from "react";
import { toast, ToastContainer } from "react-toastify";
import DataPagination from "../common/dataPagination/DataPagination";
import DataTable from "../common/dataTable/DataTable";
import EditDeleteButton from "../common/editAndDeleteButton/EditDeleteButton";
import EditModal from "../common/editModal/EditModal";
import SectionTitle from "../common/sectionTitle/SectionTitle";
import TextInput from "../common/textInput/TextInput";
import { apiUrl } from "../../config/api";

const ManageCategory = () => {
  const notifySuccess = (text) => toast.success(text);
  const notifyError = (text) => toast.error(text);
  const [categories, setCategories] = useState([]);
  const [modalData, setModalData] = useState({});

  // pagination functions
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [dataPerPage] = useState(10);
  // Get current data
  const indexOfLastData = currentPage * dataPerPage;
  const indexOfFirstData = indexOfLastData - dataPerPage;
  const currentData = data.slice(indexOfFirstData, indexOfLastData);

  // Change page
  const paginate = (e, pageNumber) => {
    setCurrentPage(pageNumber);
    e.preventDefault();
  };

  // data load from API
  useEffect(() => {
    const url = apiUrl + "product/allCategories";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
      });
  }, [categories]);

  const deleteHandler = (id) => {
    const url = `${apiUrl}product/deleteCategory/${id}`;
    fetch(url, {
      method: "DELETE",
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data, "dataaa");
        notifySuccess("Brand Delete Successfully");
      })
      .catch((err) => {
        notifyError("Brand Not Delete Successfully");
      });
  };
  const editHandler = (data) => {
    setModalData(data);
  };
  const handleUpdate = (e) => {
    const url = `${apiUrl}product/updateCategory`;
    fetch(url, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify(modalData),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data, "dataaa");
        notifySuccess("Brand Data Updated Successfully");
      })
      .catch((err) => {
        notifyError("Brand Data Updated Failed");
      });

    e.preventDefault();
  };
  const handleChange = (e) => {
    const newData = { ...modalData };
    newData[e.target.name] = e.target.value;
    setModalData(newData);
  };

  return (
    <div>
      <ToastContainer />
      <SectionTitle label="Manage Category" />
      <DataTable>
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Brand Name</th>
            <th scope="col">Total Products</th>
            <th scope="col">Action</th>
          </tr>
        </thead>
        <tbody>
          {currentData.map((item, index) => (
            <tr key={index}>
              <td>{index + 1}</td>
              <td>{item.title}</td>
              <td>Otto</td>
              <td>
                <EditDeleteButton
                  onClick={(e) => editHandler(item)}
                  data-bs-toggle="modal"
                  data-bs-target="#editModal"
                  icon={faEdit}
                />
                <EditDeleteButton
                  onClick={() => deleteHandler(item._id)}
                  design="redDelete"
                  icon={faTrashAlt}
                />

                <EditModal
                  modalTitle="Update Brand Name"
                  handleSubmit={handleUpdate}
                >
                  <TextInput
                    onChange={(e) => handleChange(e)}
                    name="categoryName"
                    value={modalData.categoryName}
                    label="Brand Name"
                  />
                </EditModal>
              </td>
            </tr>
          ))}
        </tbody>
      </DataTable>

      <DataPagination
        dataPerPage={dataPerPage}
        totalData={data.length}
        paginate={paginate}
        currentPage={currentPage}
        indexOfLastData={indexOfLastData}
        indexOfFirstData={indexOfFirstData}
      />
    </div>
  );
};

export default ManageCategory;
