import React, { useEffect, useState } from "react";
import ColumnDivider from "../common/culumnDivider/ColumnDivider";
import CustomButton from "../common/customButton/CustomButton";
import DataPagination from "../common/dataPagination/DataPagination";
import DataTable from "../common/dataTable/DataTable";
import TextInput from "../common/textInput/TextInput";
import "./Sales.scss";
import { apiUrl } from "../../config/api";

const TodayPosSales = () => {
  const [searchId, setSearchId] = useState("");
  const [searchName, setSearchName] = useState("");
  const [trigger, setTrigger] = useState(0);

  // pagination functions
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [dataPerPage] = useState(10);
  // Get current data
  const indexOfLastData = currentPage * dataPerPage;
  const indexOfFirstData = indexOfLastData - dataPerPage;
  const currentData = data.slice(indexOfFirstData, indexOfLastData);

  // Change page
  const paginate = (e, pageNumber) => {
    setCurrentPage(pageNumber);
    e.preventDefault();
  };

  //search product by id from API
  useEffect(() => {
    const url = `${apiUrl}search/findSaleById/${searchId}`;
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
        console.log(data);
      });
  }, [searchId]);

  //search sale by customer name from API
  useEffect(() => {
    const url = `${apiUrl}search/findSaleByCustomerName/${searchName}`;
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
        console.log(data);
      });
  }, [searchName]);

  //sale data load from API
  useEffect(() => {
    setLoading(true);
    const url = apiUrl + "pos/allSale";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setData(data);
        console.log(data, "ok data");
        setLoading(false);
      });
  }, [trigger]);

  const handleSubmit = (e) => {
    setTrigger((x) => x + 1);
    e.preventDefault();
    e.target.reset();
  };
  const handleChange = (e) => {
    const newData1 = { ...searchId };
    const newData2 = { ...searchId };
    newData1[e.target.name] = e.target.value;
    newData2[e.target.name] = e.target.value;
    setSearchId(newData1.searchById);
    setSearchName(newData2.searchCustomerName);
  };

  return (
    <div>
      <form onSubmit={handleSubmit} className="pos_sales_filter">
        <ColumnDivider design="pos_sales_input_divider">
          <TextInput
            onChange={(e) => handleChange(e)}
            name="searchById"
            label="Bill Number"
          />
          <TextInput
            onChange={(e) => handleChange(e)}
            name="searchCustomerName"
            label="Start Date"
          />
          <CustomButton
            design="pos_product_list_search_btn"
            type="submit"
            label="Reset"
            icon=""
          />
        </ColumnDivider>
      </form>

      <DataTable>
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Invoice No.</th>
            <th scope="col">Customer</th>
            <th scope="col">Items</th>
            <th scope="col">Date</th>
            <th scope="col">Discount</th>
            {/* <th scope="col">Receivable</th> */}
            <th scope="col">Items Price</th>
            <th scope="col">Shipping Price</th>
            <th scope="col">Tax Price</th>
            {/* <th scope="col">Product Returned</th> */}
            {/* <th scope="col">Due</th> */}
            <th scope="col">Total Cost</th>
            <th scope="col">Profit</th>
            <th scope="col">Status</th>
          </tr>
        </thead>
        <tbody>
          {currentData.map((item, index) => (
            <tr loading={loading}>
              <td>{index + 1}</td>
              <td>{item._id}</td>
              <td>{item.customer?.name}</td>
              <td>{item.sellingProducts.length}</td>
              <td>{item.purchaseDate}</td>
              <td>N/A</td>
              <td>{item.itemsPrice}</td>
              <td>{item.shippingPrice}</td>
              <td>{parseInt(item.taxPrice).toFixed(2)}</td>
              <td>{item.totalPrice}</td>

              <td>@mdo</td>
              <td>Paid</td>
            </tr>
          ))}
        </tbody>
      </DataTable>
      <DataPagination
        dataPerPage={dataPerPage}
        totalData={data.length}
        paginate={paginate}
        currentPage={currentPage}
        indexOfLastData={indexOfLastData}
        indexOfFirstData={indexOfFirstData}
      />
    </div>
  );
};

export default TodayPosSales;
