import React, { Component } from 'react';
import ApexCharts from 'apexcharts'
import ReactApexChart from 'react-apexcharts'
import './Radialbar.scss'


class Radialbar_1 extends Component {

    constructor(props) {
        super(props);

        this.state = {

            series: [54, 69, 67, 83],
            options: {
                chart: {
                    height: 350,
                    type: 'radialBar',
                },
                plotOptions: {
                    radialBar: {
                        dataLabels: {
                            name: {
                                fontSize: '22px',
                            },
                            value: {
                                fontSize: '16px',
                            },
                            total: {
                                show: true,
                                label: 'Total Profit',
                                formatter: function (w) {
                                    // By default this function returns the average of all series. The below is just an example to show the use of custom formatter function
                                    return 249
                                }
                            }
                        }
                    }
                },
                labels: ['Sale', 'Tex', 'Shipping', 'Total Cost'],
            },


        };
    }


    render() {
        return (
            <div className="radialbar_1">
                <div class="des_box_header m-0 ">
                    <h4 class="des_box_title radialbar_1_title p-0 m-0">Daily Profit, Sales</h4>
                <hr />

                </div>
                <ReactApexChart options={this.state.options} series={this.state.series} type="radialBar" height={290} />
            </div>
        );
    }
}

export default Radialbar_1;