import React from 'react';
import './TableInput.scss'


const TableInput = ({ type = "text", label, width = "20", ...rest }) => {

    return (
        <td className="table_input_box" style={{ marginRight: "5px", width: `${width}%` }}>
            <label >{label}</label>
            <input type={type} className="table_input" {...rest} placeholder={label} />
        </td>
    );
};

export default TableInput