import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import './DataPagination.scss'
import ReactPaginate from 'react-paginate';

const DataPagination = ({ dataPerPage, totalData, paginate, currentPage, indexOfLastData, indexOfFirstData }) => {

    const pageNumbers = [];

    for (let i = 1; i <= Math.ceil(totalData / dataPerPage); i++) {
        pageNumbers.push(i);
    }




    return (
        <div className="pagination_section">
            <p className="showing_box">Showing {indexOfFirstData + 1} to {indexOfLastData} of {totalData} entries</p>

            <nav className="pagination_custom_box">
                <ul className="">
                    <li><NavLink onClick={(e) => paginate(e, currentPage - 1)} to="#">Previous</NavLink> </li>
                    {
                        pageNumbers.map((number, index) => (

                            <li key={number}><NavLink onClick={(e) => paginate(e, number)} className={(navInfo) => navInfo.isActive ? "paginationActive" : ''} to={`!#`}>  {number}</NavLink> </li>
                        ))
                    }
                    <li><NavLink onClick={(e) => paginate(e, currentPage + 1)} to="#">Next</NavLink></li>
                </ul>
            </nav>
        </div>
    );
};

export default DataPagination;