import React, { useEffect, useState } from "react";
import TextInput from "../common/textInput/TextInput";
import FileInput from "../common/fileInput/FileInput";
import TextArea from "../common/textArea/TextArea";
import SelectInput from "../common/selectInput/SelectInput";
import SectionTitle from "../common/sectionTitle/SectionTitle";
import CustomButton from "../common/customButton/CustomButton";
import ColumnDivider from "../common/culumnDivider/ColumnDivider";
import { faHome } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import { apiUrl } from "../../config/api";

const AddProduct = () => {
  const [brands, setBrands] = useState([]);
  const [categories, setCategories] = useState([]);
  const [newProduct, setNewProduct] = useState({});
  const notifySuccess = () => toast.success("New Brand Added");
  const notifyError = () => toast.error("Brad Added Failed");

  // all brand loader
  useEffect(() => {
    const url = apiUrl + "product/allBrands";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setBrands(data);
      });
  }, [brands]);

  // all category loader
  useEffect(() => {
    const url = apiUrl + "product/allCategories";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setCategories(data);
      });
  }, [categories]);

  const handleChange = (e) => {
    const newData = { ...newProduct };
    newData[e.target.name] = e.target.value;
    newData.purchaseQuantity = 0;
    setNewProduct(newData);
  };
  const [imageURL, setImageURL] = useState({});
  const handleChangeFile = (e) => {
    const imageData = new FormData();
    imageData.set("key", "59b9f92bf1b5d3036dd00cceba773135");
    imageData.append("image", e.target.files[0]);

    axios
      .post("https://api.imgbb.com/1/upload", imageData)
      .then(function (response) {
        setImageURL({
          productImage: response.data.data.display_url,
        });
      })
      .catch(function (error) {});
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const newData = { ...newProduct, ...imageURL };

    // setNewProduct(newData)

    console.log(newData);

    const url = apiUrl + "product/createNewProduct";

    fetch(url, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify(newData),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data, "ok");
        notifySuccess();
        e.target.reset();
      })
      .catch((err) => {
        console.log("Data was not inserted Successfully");
        notifyError();
      });
  };
  return (
    <>
      <ToastContainer />
      <form onSubmit={handleSubmit} className="custom_form_style">
        <SectionTitle label="Add Product" />
        <TextInput
          onChange={(e) => handleChange(e)}
          name="productName"
          label="Product Name"
        />
        <TextInput
          onChange={(e) => handleChange(e)}
          name="productCode"
          label="Product Code"
        />
        <ColumnDivider>
          <SelectInput
            onChange={(e) => handleChange(e)}
            name="brand"
            options={brands}
            label="Brand"
          />
          <SelectInput
            onChange={(e) => handleChange(e)}
            name="category"
            options={categories}
            label="Category"
          />
        </ColumnDivider>

        <ColumnDivider>
          <TextInput
            onChange={(e) => handleChange(e)}
            name="buyPrice"
            label="Buy Price"
          />
          <TextInput
            onChange={(e) => handleChange(e)}
            name="sellPrice"
            label="Sell Price"
          />
        </ColumnDivider>

        <TextInput
          onChange={(e) => handleChange(e)}
          name="openingStock"
          label="Opening Stock"
        />

        <FileInput
          onChange={(e) => handleChangeFile(e)}
          label="Product Image"
          name="productImage"
        />
        <TextArea
          onChange={(e) => handleChange(e)}
          name="description"
          label="Product Description"
        />

        <CustomButton type="submit" label="Submit" icon={faHome} />
      </form>
    </>
  );
};

export default AddProduct;
