import React from 'react';
import './ItemCard_1.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBox, faBoxes, faDolly, faHome } from '@fortawesome/free-solid-svg-icons';
import { faProductHunt } from '@fortawesome/free-brands-svg-icons';


const ItemCard_1 = (props) => {
    const { title, action, icon, iconColor, iconBgColor } = props.data
    return (

        <div className="itemCard_1 ">
            <div className="d-flex align-items-center justify-content-between">
                <div style={{ background: iconBgColor }} className="itemCard_1_icon_box  rounded-circle">
                    <FontAwesomeIcon style={{ color: iconColor }} className="itemCard_1_icon" icon={icon} />

                </div>
                <div className="card_content" >
                    <h3 className="mb-0 ">{action}</h3>
                    <p className="text-mute mb-0">{title}</p>
                </div>
            </div>
        </div>

    );
};

export default ItemCard_1;