import React, { useEffect, useState } from "react";
import Barchart_1 from "../common/dasElements/barchart_1/Barchart_1";
import ItemCard_1 from "../common/dasElements/itemCard_1/ItemCard_1";
import ItemCard_2 from "../common/dasElements/itemCard_2/ItemCard_2";
import Radialbar_1 from "../common/dasElements/radialbar_1/Radialbar_1";
import "./DashboardContent.scss";
import shippingAnimation from "../../assets/Images/shopping.gif";
import ColumnChart_1 from "../common/dasElements/columnChart_1/ColumnChart_1";
import {
  faBox,
  faBoxes,
  faDolly,
  faHome,
} from "@fortawesome/free-solid-svg-icons";
import { faProductHunt } from "@fortawesome/free-brands-svg-icons";
import { apiUrl } from "../../config/api";
// import Radialbar_2 from '../common/dasElements/itemCard_2/ItemCard_2';

const DashboardContent = () => {
  const [products, setProducts] = useState([]);
  const [brands, setBrands] = useState([]);
  const [categories, setCategories] = useState([]);
  const [sales, setSales] = useState(0);
  const [supplier, setSupplier] = useState([]);

  const [check, setCheck] = useState(false);

  const [customer, setCustomer] = useState([]);

  // customer data load from API
  useEffect(() => {
    const url = apiUrl + "people/allCustomer";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setCustomer(data);
      });
  }, []);

  // allSupplier data load from API
  useEffect(() => {
    const url = apiUrl + "people/allSupplier";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setSupplier(data.length);
        console.log(data.length, "ok data");
      });
  }, []);

  // total product fetch
  useEffect(() => {
    const url = apiUrl + "product/allProducts";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setProducts(data);
        // console.log(data, 'ok data');
      });
  }, []);

  // all brand loader
  useEffect(() => {
    const url = apiUrl + "product/allBrands";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setBrands(data);
      });
  }, []);

  // all category loader
  useEffect(() => {
    const url = apiUrl + "product/allCategories";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setCategories(data);
      });
  }, []);

  //sale data load from API
  useEffect(() => {
    const url = apiUrl + "pos/allSale";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setSales(data);
        // console.log(data, 'ok data');
      });
  }, []);

  const itemData_1 = [
    {
      title: "Total Products",
      action: "000" + products.length,
      icon: faProductHunt,
      iconColor: "#5948d5",
      iconBgColor: "#f2f1fa",
    },
    {
      title: "Total Brands",
      action: "000" + brands.length,
      icon: faBox,
      iconColor: "#007EFF",
      iconBgColor: "#c6e2ff",
    },
    {
      title: "Total Categories",
      action: "000" + categories.length,
      icon: faBoxes,
      iconColor: "#ea9715",
      iconBgColor: "#faedda",
    },
    {
      title: "Total Sales",
      action: "00" + sales.length,
      icon: faDolly,
      iconColor: "#fb3d4e",
      iconBgColor: "#fbeeee",
    },
  ];
  const itemData_2 = [
    {
      title: "New Customer",
      action: 339,
      percent: "p_bar_1",
      color: "#9A51D4",
    },

    {
      title: "New Supplier",
      action: 15,
      percent: "p_bar_2",
      color: "#E3931D",
    },
    {
      title: "Total Customer",
      action: customer.length + 5140,
      percent: "p_bar_3",
      color: "#F43D53",
    },
    {
      title: "Total Supplier",
      action: supplier + 405,
      // action: 4,
      // action: sales.length,
      percent: "p_bar_4",
      color: "#ABE5A1",
    },
  ];

  return (
    <main>
      <div className="card_box_1">
        {itemData_1.map((item, index) => (
          <ItemCard_1 data={item} />
        ))}
      </div>
      <div className="div_2">
        <Barchart_1 />
        <Radialbar_1 />
        <img className="shipping_animation" src={shippingAnimation} alt="" />
      </div>
      <div className="div_container pb-5">
        <div className="div_3">
          {itemData_2.map((item, index) => (
            <ItemCard_2 data={item} />
          ))}
        </div>
        <div className="div_4">
          <ColumnChart_1 />
        </div>
      </div>
    </main>
  );
};

export default DashboardContent;
