import { faHome } from "@fortawesome/free-solid-svg-icons";
import React from "react";
import { useNavigate, useParams } from "react-router";
import CustomButton from "../common/customButton/CustomButton";
import PageTitle from "../common/pageTitle/PageTitle";
import AddPurchase from "./AddPurchase";
import ManagePurchases from "./ManagePurchases";
import "./Purchase.scss";

const breadcrumbData = [
  {
    label: "Purchase",
    link: "/dashboard/Purchase",
  },
];

const Purchase = () => {
  const navigate = useNavigate();
  const { ppId, pagesId } = useParams();

  const navigateHandler_1 = () => {
    navigate("/dashboard/purchase/addPurchase");
  };
  const navigateHandler_2 = () => {
    navigate("/dashboard/purchase/managePurchases");
  };
  return (
    <div className="products_section">
      <PageTitle title="Purchase" breadcrumbData={breadcrumbData} />

      <div className="product_section_box">
        <CustomButton
          label="Add Purchase"
          onClick={() => navigateHandler_1()}
          icon={faHome}
        />
        <CustomButton
          label="Purchases"
          onClick={() => navigateHandler_2()}
          icon={faHome}
        />
      </div>
      <div className="products_section_body">
        {pagesId === "purchase" && ppId === undefined ? <AddPurchase /> : ""}
        {ppId === "addPurchase" ? (
          <AddPurchase />
        ) : ppId === "managePurchases" ? (
          <ManagePurchases />
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

export default Purchase;
