import React from 'react';
import './TextInput.scss'
const TextInput = ({ type = "text", label, width = "100", ...rest }) => {
    return (
        <div className="text_input_box" style={{ marginRight: "5px", width: `${width}%` }}>
            <label >{label}</label>
            <input type={type} className="text_input" {...rest} placeholder={label} />
        </div>
    );
};

export default TextInput