import { faPlus, faTrash } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState, useEffect } from "react";
import ColumnDivider from "../common/culumnDivider/ColumnDivider";
import CustomButton from "../common/customButton/CustomButton";
import DataTable from "../common/dataTable/DataTable";
import SectionTitle from "../common/sectionTitle/SectionTitle";
import SelectInput from "../common/selectInput/SelectInput";
import TableInput from "../common/tableInput/TableInput";
import TextInput from "../common/textInput/TextInput";
import rightMarkImage from "../../assets/Images/right.png";
import { ToastContainer, toast } from "react-toastify";
import { apiUrl } from "../../config/api";

const AddPurchase = () => {
  const [newPurchase, setNewPurchase] = useState({});
  const [supplier, setSupplier] = useState([]);
  const [products, setProducts] = useState([]);
  const [quantity, setQuantity] = useState(0);
  const [rate, setRate] = useState(0);
  const [amount, setAmount] = useState(0);
  const notifySuccess = () => toast.success("New Brand Added");
  const notifyError = () => toast.error("Brad Added Failed");

  // supplier data load from API
  useEffect(() => {
    const url = apiUrl + "people/allSupplier";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setSupplier(data);
      });
  }, []);

  //products data load from API
  useEffect(() => {
    const url = apiUrl + "product/allProducts";
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        setProducts(data);
        console.log(data, "ok data");
      });
  }, []);

  const [productUpdateData, setProductUpdateData] = useState({});

  // product purchase submission
  const handleSubmit = (e) => {
    // create new purchase
    const newData = { ...newPurchase };
    newData.productQuantity = quantity;
    newData.productRate = rate;
    newData.totalAmount = amount;
    setNewPurchase(newData);
    const url = apiUrl + "pos/createNewPurchase";
    fetch(url, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify(newData),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data, "ok");
        notifySuccess();
        e.target.reset();
      })
      .catch((err) => {
        console.log("Data was not inserted Successfully");
        notifyError();
      });

    e.preventDefault();
    e.target.reset();
  };
  // on change handler
  const handleChange = (e) => {
    const newData = { ...newPurchase };
    newData[e.target.name] = e.target.value;
    setNewPurchase(newData);
  };

  // amount calculation
  useEffect(() => {
    let Quantity = parseInt(quantity);
    let Rate = parseInt(rate);
    let totalMoney = Quantity * Rate;
    setAmount(totalMoney);
    console.log(Quantity);
    console.log(Rate);
    console.log(amount);
  }, [rate, quantity, amount]);

  return (
    <div className="add_purchase_section">
      <ToastContainer />
      <div className="add_purchase_btn">
        <CustomButton label="Add Supplier" icon={faPlus} />
      </div>
      <SectionTitle label="Create Purchase" />
      <form onSubmit={handleSubmit} className="">
        <ColumnDivider design="supplier_form_style">
          <SelectInput
            onChange={(e) => handleChange(e)}
            name="supplier"
            options={supplier}
            label="Supplier Name"
          />
          <TextInput
            type="date"
            onChange={(e) => handleChange(e)}
            name="purchaseDate"
            label="Purchase Date"
          />

          {/* <TextInput onChange={(e) => handleChange(e)} value={products.productCode} name='brandName' label="Product Code" /> */}
          <SelectInput
            onChange={(e) => handleChange(e)}
            name="product"
            options={products}
            label="Product Name - Code"
          />
        </ColumnDivider>

        <DataTable>
          <thead>
            <tr>
              <th scope="col">Rate</th>
              <th scope="col">Qty</th>
              <th style={{ textAlign: "center" }} scope="col">
                Total Amount
              </th>
              {/* <th scope="col"><FontAwesomeIcon icon={faTrash} /></th> */}
            </tr>
          </thead>
          <tbody>
            <tr>
              <TableInput
                onChange={(e) => setQuantity(e.target.value)}
                name="productQuantity"
                label=""
              />
              <TableInput
                onChange={(e) => setRate(e.target.value)}
                name="productRate"
                label=""
              />
              {/* <TableInput value={amount || 0}  disabled/> */}
              <td style={{ textAlign: "center" }}>{amount || 0} Taka</td>
            </tr>
          </tbody>
        </DataTable>
        <CustomButton
          type="button"
          data-bs-toggle="modal"
          data-bs-target="#staticBackdrop"
          label="Payment"
          icon=""
        />

        {/* modals payment confirm*/}
        <div
          className="modal fade"
          id="staticBackdrop"
          data-bs-backdrop="static"
          data-bs-keyboard="false"
          tabindex="-1"
          aria-labelledby="staticBackdropLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-body text-center mb-5 modal_body_desing">
                <img src={rightMarkImage} className="img_responsive" alt="" />
                <h1>Are You Sure?</h1>
                <p>
                  Do you really want to submit these records? This process
                  cannot be undone.
                </p>
                <div class="btn-group">
                  <button
                    type="button"
                    className="btn btn-danger btn-lg mr-2 rounded-lg"
                    data-bs-dismiss="modal"
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    className="btn btn-success btn-lg rounded-lg"
                  >
                    Confirm
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
};

export default AddPurchase;
