import React, { useEffect, useState } from 'react';
import './ItemCard_2.scss'



const ItemCard_2 = (props) => {
    const { title, action, percent, color } = props.data
    console.log(action, 'me');
    return (
        <>
            <div className="itemCard_2">
                <div className="text-center">
                    <div className="itemCard_2_icon_box py-3">

                        <div className={`progress ${percent}`}>
                            <span className="progress_left">
                                <span className="progress_bar " style={{borderColor: color}}></span>
                            </span>
                            <span className="progress_right">
                                <span className="progress_bar" style={{borderColor: color}}></span>
                            </span>
                            <div className="progress_value" style={{color: color}}>{action}</div>
                        </div>

                    </div>
                    <div className="item_card_2_content py-2">
                        <h3 className="mb-0  pb-3">{title}</h3>
                        {/* <p className="text-mute mb-0">{title}</p> */}
                    </div>
                </div>
            </div>

        </>
    );
};

export default ItemCard_2;