import React, { useState } from "react";
import CustomButton from "../common/customButton/CustomButton";
import SectionTitle from "../common/sectionTitle/SectionTitle";
import TextArea from "../common/textArea/TextArea";
import TextInput from "../common/textInput/TextInput";
import { ToastContainer, toast } from "react-toastify";
import { apiUrl } from "../../config/api";

const AddCustomer = () => {
  const notifySuccess = (text) => toast.success(text);
  const notifyError = (text) => toast.error(text);
  const [newCustomer, setNewCustomer] = useState({});
  const handleSubmit = (e) => {
    fetch(apiUrl + "createNewCustomer", {
      method: "POST",
      headers: {
        "Content-type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify(newCustomer),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data, "ok");
        notifySuccess("Data Submitted");
      })
      .catch((err) => {
        console.log("Data was not inserted Successfully");
        notifyError("Failed");
      });

    e.preventDefault();
    console.log(newCustomer);
    e.target.reset();
  };
  const handleChange = (e) => {
    const newData = { ...newCustomer };
    newData[e.target.name] = e.target.value;
    setNewCustomer(newData);
  };
  return (
    <>
      <ToastContainer />

      <form onSubmit={handleSubmit} className="custom_form_style">
        <SectionTitle label="Add Customer" />
        <TextInput
          onChange={(e) => handleChange(e)}
          name="name"
          label="Customer Name"
        />
        <TextInput
          onChange={(e) => handleChange(e)}
          name="email"
          label="Email"
        />
        <TextInput
          onChange={(e) => handleChange(e)}
          name="phone"
          label="Phone"
        />
        <TextArea
          onChange={(e) => handleChange(e)}
          name="address"
          label="Address"
        />
        <CustomButton type="submit" label="Submit" icon="" />
      </form>
    </>
  );
};

export default AddCustomer;
